import React, { useState } from "react";
import { AiOutlineMenu, AiOutlineClose } from "react-icons/ai";
import logo from "../assets/logo_sec-removebg.png";
import { Box, Typography } from "@mui/material";

const Navbar = () => {
  const [nav, setNav] = useState(false);
  const handleNav = () => {
    setNav(!nav);
  };

  return (
    <div style={{ background: "black" }} className="w-full h-[90px]">
      <div className="max-w-[1240px] mx-auto px-4 flex justify-between items-center h-full">
        <Box>
          <a
            href="/"
            style={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <Box
              component="img"
              alt="profile"
              src={logo}
              height="72px"
              width="90px"
              sx={{ objectFit: "cover" }}
            />
            <Box
              display="flex"
              justifyContent="center"
              flexDirection="column"
              alignItems="center"
            >
              <Typography
                variant="h1"
                color="white"
                fontSize="20px"
                marginLeft="-23px"
                textTransform="uppercase"
                sx={{
                  fontWeight: 300,
                  fontFamily: "Montserrat",
                  letterSpacing: "2px",
                }}
              >
                rtizeLight
              </Typography>
              <Typography
                variant="h5"
                color="white"
                fontSize="10px"
                marginLeft="-32px"
                textTransform="uppercase"
                sx={{ fontWeight: 300, fontFamily: "Montserrat" }}
              >
                Lighting Endeavor
              </Typography>
            </Box>
          </a>
        </Box>

        <div className="hidden md:flex">
          <ul className="flex text-white items-center">
            <li>Home</li>
            <li>About</li>
            <li>Products</li>
            <li>FAQ</li>
            <button className="ml-4">Contact Us</button>
          </ul>
        </div>

        {/* Hamburger menu */}
        <div onClick={handleNav} className="block md:hidden">
          {nav ? (
            <AiOutlineClose size={30} className="text-white" />
          ) : (
            <AiOutlineMenu size={30} className="text-white" />
          )}
        </div>

        {/* Mobile Menu */}
        <div
          className={
            nav
              ? "w-full bg-black text-white absolute top-[90px] left-0 flex justify-center text-center"
              : "absolute left-[-100%]"
          }
        >
          <ul>
            <li>Home</li>
            <li>About</li>
            <li>Products</li>
            <li>FAQ</li>
            <button className="ml-4">Contact Us</button>
          </ul>
        </div>
      </div>
    </div>
  );
};

export default Navbar;
